import React, { useState } from 'react';

import { Col, Row, FormGroup, Label } from 'reactstrap';
import AsyncSelect from 'react-select/async';

import api from '../../api'


const Customer = (props) => {
    const [groupId, setGroupId] = useState(
        null
    );

    const promiseCustomerGroupOptions = inputValue => {
        const url = `customer-groups${inputValue ? '?filter[text]=' + inputValue : ''}`
        return api.get(url)
            .then(response => {
                const options = response.data.data.map(group => ({ value: group.id, label: group.attributes.description }));
                return options;
            })
            .catch(err => {
                console.log('some error', err);
            });
    };

    const promiseCustomerOptions = inputValue => {
        const url = `customers${inputValue ? '?filter[text]=' + inputValue : ''}${groupId ? '&filter[group_id]=' + groupId : ''}`
        return api.get(url)
            .then(response => {
                const options = response.data.data.map(customer => ({ value: customer.id, label: customer.attributes.business_name }));
                return options;
            })
            .catch(err => {
                console.log('some error', err);
            });
    };

    const handleChangeGroup = (event) => {
        const value = event ? event.value : null
        props.onChange(value, 'customer_group_id')
        setGroupId(value)
    }
    return (
        <Row>
            <Col md={6}>
                <FormGroup>
                    <Label for="exampleUrl">Gruppo Cliente</Label>
                    <AsyncSelect
                        isClearable
                        cacheOptions
                        loadOptions={promiseCustomerGroupOptions}
                        defaultOptions
                        onChange={(event) => handleChangeGroup(event)}
                    />
                </FormGroup>
            </Col>
            <Col md={6}>
                <FormGroup>
                    <Label for="exampleUrl">Cliente</Label>
                    <AsyncSelect
                        isClearable
                        loadOptions={promiseCustomerOptions}
                        defaultOptions
                        onChange={(event) => props.onChange(event.value, 'customer_id')}
                    />
                </FormGroup>
            </Col>
        </Row>
    );
};

export default Customer;
