import React from 'react';

import { Col, Row, FormGroup, Label, Input } from 'reactstrap';

const DateRange = (props) => {
    return (
        <Row>
            <Col md={6}>
                <FormGroup>
                    <Label for="from">Da</Label>
                    <Input
                        type="date"
                        name="from"
                        id="from"
                        placeholder="Data inizio"
                        defaultValue={props.fromStartDate}
                        onChange={(event) => props.onChange(event.target.value, 'from')}
                    />
                </FormGroup>
            </Col>
            <Col md={6}>
                <FormGroup>
                    <Label for="exampleDate">A</Label>
                    <Input
                        type="date"
                        name="date"
                        id="to"
                        placeholder="Data Fine"
                        defaultValue={props.toStartDate}
                        onChange={(event) => props.onChange(event.target.value, 'to')}
                    />
                </FormGroup>
            </Col>
        </Row>
    );
};

export default DateRange;
