import Page from 'components/Page';
import React from 'react';
import { connect } from 'react-redux';
import { Card, CardBody, CardHeader, Col, Row, Table, Form, Button } from 'reactstrap';
import NumberFormat from 'react-number-format';


import * as actions from '../store/actions/index';
import DateRange from '../components/Filter/DateRange'
import CustomerFilter from '../components/Filter/Customer'
import api from '../api'
import { createQueryString } from '../utils/queryString';
import NotificationSystem from 'react-notification-system';
import { NOTIFICATION_SYSTEM_STYLE } from 'utils/constants';
import {
    MdError
} from 'react-icons/md';
import RowNoData from '../components/RowNoData';
import formatDate from '../utils/formatDate'

import BasePage from './BasePage';


let date = new Date()

class CustomerPointsPage extends BasePage {

    state = {
        filters: {
            customer_group_id: null,
            customer_id: null,
            from: formatDate(new Date(date.getFullYear(), date.getMonth(), 1)),
            to: formatDate(new Date()),
        }
    }

    handleSubmit = () => {
        const { searchClicked } = this.state
        this.setState({
            searchClicked: !searchClicked
        })
        this.props.onLoadStats(null, null, this.state.filters, ['customer'], null)
    }

    handleFilterChange = (value, name) => {
        const { filters } = this.state
        filters[name] = value
        this.setState({
            filters
        })
    }

    handlePdfClick = () => {
        const queryString = createQueryString(null, null, this.state.filters, ['customer'], null);
        api.get(`purchase-aggregations/pdf${queryString}`)
            .then(response => {
                fetch(response.data, {
                    responseType: 'blob'
                }).then(response => {
                    const fileName = `punti_cliente_${this.state.filters.from}_${this.state.filters.to}.pdf`
                    const url = window.URL.createObjectURL(new Blob([response.data]));
                    const link = document.createElement('a');
                    link.href = url;
                    link.setAttribute('download', fileName);
                    document.body.appendChild(link);
                    link.click();
                })
            })
            .catch(error => {
                if (!this.notificationSystem) {
                    return;
                }

                this.notificationSystem.addNotification({
                    title: <MdError />,
                    message: 'Errore nello scaricamento',
                    level: 'error',
                });
            });
    }

    render() {
        let {
            stats
        } = this.props
        let rows = <RowNoData colspan={5} />
        let sumValue = 0
        let sumPoints = 0
        let sumValuePrevYear = 0
        let sumPointsPrevYear = 0
        if (this.props.loading === false && this.props.error === false && this.props.stats !== null) {
            stats.sort((a, b) => (parseFloat(a.attributes.spent_amount) > parseFloat(b.attributes.spent_amount)) ? -1 : 1)
            rows = stats.map((row, index) => {
                sumValue += parseFloat(row.attributes.spent_amount)
                sumPoints += parseInt(row.attributes.earned_points)
                sumValuePrevYear += parseFloat(row.attributes.spent_amount_previous_year)
                sumPointsPrevYear += parseInt(row.attributes.earned_points_previous_year)
                return <tr key={index}>
                    <td className="text-left">{this.props.customers[row.attributes.customer_id].business_name}</td>
                    <td className="text-right"><NumberFormat value={row.attributes.spent_amount} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                    <td className="text-right">{row.attributes.earned_points}</td>
                    <td className="text-right"><NumberFormat value={row.attributes.spent_amount_previous_year} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                    <td className="text-right">{row.attributes.earned_points_previous_year}</td>
                </tr>
            })
        }
        return (
            <Page title="Punti clienti" breadcrumbs={[{ name: 'punti clienti', active: true }]}>
                <Row>
                    <Col>
                        <Card className="mb-3">
                            <CardHeader>
                                Filtri
                            </CardHeader>
                            <CardBody>
                                <Form onSubmit={this.handleSubmit}>
                                    <DateRange onChange={this.handleFilterChange.bind(this)} fromStartDate={this.state.filters.from} toStartDate={this.state.filters.to}/>
                                    <CustomerFilter onChange={this.handleFilterChange.bind(this)}/>
                                    <Row>
                                        <Col>
                                            <Button
                                                color="primary"
                                                disabled={this.props.loading && this.state.searchClicked}
                                                onClick={this.handleSubmit}>
                                                Cerca
                                            </Button>
                                        </Col>
                                    </Row>
                                </Form>
                            </CardBody>
                        </Card>
                        <Card className="mb-3">
                            <CardHeader>
                                <Button
                                    className="float-right"
                                    color="secondary"
                                    icon="pdf"
                                    onClick={this.handlePdfClick}
                                    disabled={!this.props.pdfAvaiable}>
                                PDF
                                </Button>
                            </CardHeader>
                            <CardBody>
                                <Table responsive>
                                    <thead>
                                        <tr>
                                            <th className="text-left">Cliente</th>
                                            <th className="text-right">Valore speso €</th>
                                            <th className="text-right">Punti</th>
                                            <th className="text-right">Valore speso anno precedente €</th>
                                            <th className="text-right">Punti anno precedente</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {rows}
                                    </tbody>
                                    <tfoot>
                                        <tr>
                                            <th>TOTALE:</th>
                                            <td className="text-right"><NumberFormat value={sumValue} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                                            <td className="text-right">{sumPoints}</td>
                                            <td className="text-right"><NumberFormat value={sumValuePrevYear} displayType={'text'} thousandSeparator={'.'} decimalSeparator={','} decimalScale={2} fixedDecimalScale={true} renderText={value => <div>{value}</div>} /></td>
                                            <td className="text-right">{sumPointsPrevYear}</td>
                                        </tr>
                                    </tfoot>
                                </Table>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                <NotificationSystem
                    dismissible={false}
                    ref={notificationSystem =>
                        (this.notificationSystem = notificationSystem)
                    }
                    style={NOTIFICATION_SYSTEM_STYLE}
                />
            </Page>
        );
    }
}

const mapStateToProps = state => {
    return {
        stats: state.statistics.pointsStatistics,
        customers: state.statistics.customers,
        loading: state.statistics.loading,
        error: state.statistics.error,
        pdfUrl: state.statistics.pointsStatisticsPdfUrl,
        pdfAvaiable: state.statistics.pdfAvaiable
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onLoadStats: (page, perPage, filters, include, order) => dispatch(actions.loadPointStatCustomers(page, perPage, filters, include, order)),
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(CustomerPointsPage);
